import React,{useState} from 'react'


export default function TextForm(props) {
  const handleUpClick = ()=>{
    // console.log("button was clicked"+text);
    let newText=text.toUpperCase();
    setText(newText);
  }
  const handleLoClick = ()=>{
    // console.log("button was clicked"+text);
    let newText=text.toLowerCase();
    setText(newText);
  }
  const handleInvClick = ()=>{
    let word=text.split(" ");
    let newText=word.map((word)=>{
      let newWord="";
      for(let i=0; i<word.length; i++){
        let char=word.charAt(i);
        if(char>="A" && char<="Z"){
          char=char.toLowerCase();
        }
        else if(char>="a" && char<="z"){
          char=char.toUpperCase();
  
        }
        newWord+=char;
      }
    return newWord;
    }).join(" ");
    
    setText(newText);
  };
  const handleCopy =()=>{
    console.log("I am copy")
    var text=document.getElementById("myBox")
    text.select()
    navigator.clipboard.writeText(text.value)
  }
  const handleOnChange = (event)=>{
    // console.log("onchange");
    setText(event.target.value);
  }
  const[text, setText] = useState("");
  // setText("this is new text"); //way to change state 
  return (
    <>
    <div>
        <div className="mb-3" style={{color : props.mode==='dark'?'white':'black'}}>
          <h3>{props.heading}</h3>
        <textarea className="form-control" onChange={handleOnChange} style={{backgroundColor : props.mode==='dark'?'grey':'white',color:props.mode==='dark'?'white':'black'}} value = {text} id="myBox" rows="8"></textarea>
        </div>
        <button className="btn btn-primary mx-2" onClick={handleUpClick}>convert to Uppercase</button>
        <button className="btn btn-primary mx-2" onClick={handleLoClick}>convert to Lowercase</button>
        <button className="btn btn-primary mx-2" onClick={handleInvClick}>Invert Case</button>
        <button className="btn btn-primary mx-2" onClick={handleCopy}>Copy Text</button>

    </div>
    <div className="container" style={{color : props.mode==='dark'?'white':'black'}}>
      <h1>Text summary</h1>
      {text.split(" ").length-1} words and {text.length} Letters
    </div>
    </>
  )
}
